@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap');
body {
  margin: 0;
  font-family: 'Poppins', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.work-container{
  width: 80vw;
  padding: 5rem;
  box-shadow: 0 .4rem 1.2rem rgba(0,0,0,0.4);
  margin-top: -15rem;
  background-color: #fff;
  border-radius: 0.5rem;
}
.work-container .main-heading{
  font-size: 3rem;
  font-weight: bolder;
  margin-bottom: 4rem;
  word-spacing: 1rem;
  
}
.work-container .main-heading {
  font-size: 2rem;
  font-weight: bolder;
  margin-bottom: 4rem;
}
.work-container-sub-div{
  padding: 0 6rem;
}
.main-hero-para{
 font-size: 16px;
  color: rgb(92, 92, 92);
  margin: 2rem 0 2rem 0;
  font-weight: lighter;
  width: 85%; 
}




.our-services{
  margin: 5rem;
}
.our-services img{
  width: 90%;
  height: 85%;
  margin-top: 10rem;
  box-shadow: rgba(0, 0, 0, 0.582);
  border-radius: 7%;
}


.our-services .mini-title{
text-transform: uppercase;
font-weight: normal;
margin: 9rem 0;
font-size: 10px
}

.our-services .main-heading{
  font-weight: bolder;
}

.our-services-info {
  margin-top: 1rem;
}

.our-services .our-services-number{
  width: 3rem;
  height: 3rem;
  border-radius: 50%;
  background-color: #007bf9;
  border: transparent;
  font-size: 1.5rem;
  color: white;
  display: grid;
  place-items: center;
}

.work-container .sub-heading{
  font-size: 2rem;
  font-weight: normal;
  color: rgb(51, 51, 51);
  font-size: 24px;
  font-weight: 600;
}


.header{
  background-color: #f0f5fc;
  padding-bottom: 17rem;
}
.logo{
  height: 90px;
  width: 150px;
}
.iconosvg{
  height: 96px!important;
  width:  96px!important;
}
.iconosvg2{
  height: 46px!important;
  width:  46px!important;
}




.main-hero-container h1{
  text-transform: capitalize;
  font-weight: bolder;
}
.main-hero-para {
  font-size: 16px;
  color: rgb(92, 92, 92);
  margin: 2rem 0 2rem 0;
  font-weight: lighter;
  width: 85%;
}

.btn-style{
  width: 8rem;
  height: 3rem;
  border-radius: 2rem;
  color: rgb(255, 255, 255);
  background-color: #007bf9;
  text-transform: capitalize;
  font-family: 'Poppins', sans-serif;
  font-weight: 500;
  margin-right:10px;

}
.btn-style:hover{
  width: 8rem;
  height: 3rem;
  border-radius: 2rem;
  color: rgb(255, 255, 255);
  background-color: #007bf9;
  text-transform: capitalize;
  font-family: 'Poppins', sans-serif;
  font-weight: 600;
  margin-right:10px;

}
/*---------barra de navegacion */
.navbar-bg{
background-color: #f0f5fc;
padding: 2rem;
z-index: 99;
position: relative;
}
.particle {
  position: absolute;
  border-radius: 50%;
}
.input-group{
  position: relative!important;
}
.form-control-text{
  position: absolute;
  height: 3rem;
  border:none;
  outline-style: none;
  padding-left: 1.5rem!important;
  font-size: 15px;
  font-weight: lighter;
}
.input-group-button{
  position: absolute;
  left: 60%;
  border:none;
}

.navbar li{
  padding: 0 1rem;
  color: white;
  
}
.navbar li a{
  font-family: 'Poppins', sans-serif;
  font-size: 16px;
  color:rgb(0, 0, 0);
  font-weight: 500;
  position: relative;
}
.navbar li a:hover{
  color:rgb(0, 0, 0);
  font-weight: 700;
}
.navbar li a:active{
  color:rgb(0, 0, 0);
  font-weight: 700;
}
.navbar-light .navbar-nav .nav-link{
  color:rgb(0, 0, 0);
}
.navbar-light .navbar-nav .nav-link:hover{
  color:rgb(0, 0, 0);
  text-shadow: 0 0.2rem 0.3rem rgba(231, 231, 231, 0.5);
}
.navbar-light .navbar-nav .nav-link.active{
  color:rgb(0, 0, 0);
}

@media screen and (max-width: 980px) {
  
  .foto-hombre{
display: none;
  }
  .foto-inicio{
    display: none;
      }
  }
  



